









































import ut from '../../utils/index.js';
import marked from '../../components/marked'
import wxParse from '../../components/mpvue-wxparse/src/wxParse.vue'
export default {
	data() {
		return {
			static:'',
			swipeList: ['../../static/index/banner.jpg','../../static/index/banner.jpg'],
			indicatorDots: true,
			indicatorcolor: 'white',
			autoplay: true,
			interval: 5000,
			duration: 1000,
			circular: true,
			typeindex:0,
			typelist:[],
			serverinf:{},
			detailinf: ''
		}
	},
	components: {
		wxParse
	},
	onLoad(opt) {
		this.static=ut.static;
		this.req_detail(opt._id);
	},
	methods: {
		back_index_build(){
			wx.reLaunch({
				url: '../index/build'
			});
		},
		cg_typeindex(index){
			this.typeindex=index;
			this.serverinf=this.typelist[index];
			this.detailinf=marked(this.serverinf.detail);
		},
		go_home_pay(){
			wx.navigateTo({
				url: '../home/pay'
			})
		},
		req_detail(id){
			ut.request({
				data: {
					classid:id
				},
				url: "service/detail"
			}).then(data=>{
				this.serverinf=data[0]
				this.typelist=data;
				this.detailinf=marked(data[0].detail)
			})
		}
	}
}



